main {
	position: absolute;
	right: 7%;
	top: 6%;
	left: 7%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: end;
	    align-items: flex-end;
	padding-bottom: 0;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-direction: column;
	    flex-direction: column;
}

#avatar {
	background-image: url('./me.jpg');
	width: 75px;
	height: 75px;
	background-size: cover;
	background-position: center;
	border-radius: 50%;
}

.hero {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: end;
	    align-items: flex-end;
	text-align: right;
}

.summary {
	margin-top: 4.5rem;
	font-size: 1.3rem;
	text-align: right;
	color: #b2b9bf;
	max-width: 450px;
}

.tagline {
	text-transform: uppercase;
	font-size: 0.9rem;
	margin: 0;
	letter-spacing: 0.1rem;
}

.nm {
	color: #ffb81c;
	text-decoration: none;
}

.nm:hover {
	color: #002b49;
	text-decoration: underline;
	font-weight: bold;
}



